import React from 'react'
import axios from 'axios'
import { useState,useEffect } from 'react'
import {server} from '../index.js'
import { Button, Container ,HStack, Radio, RadioGroup} from '@chakra-ui/react'
import Loader from './Loader.jsx'
import CoinCard from './CoinCard.js'
import ErrorComponent from './ErrorComponent.jsx'
function Coins() {
    const [coins,setCoins]=useState([])
    const [loading,setLoading]=useState(true)
    const [error,setError]=useState(false)
    const [page,setPage]=useState(1)
    const [currency,setCurrency]=useState('inr')

    const currencySymbol=currency ==='inr'? '₹': currency === 'eur' ? '€' : '$';

    const changePage=(page)=>{
        setPage(page);
        setLoading(true)
    }
    const btns=new Array(132).fill(1)
    useEffect(()=>{
        const fetchCoins=async ()=>{
           try {
            const {data}=await axios.get(`${server}/coins/markets?vs_currency=${currency}&page=${page}`)
            // console.log(data)
            setCoins(data)
            setLoading(false)
           } catch (error) {
            setError(true)
            setLoading(false)
           }
        }
        fetchCoins();
    },[currency,page])
    if(error){
        return <ErrorComponent message="Error While Fetching Coins"/>
    }
  return (
    <Container maxW={'container.xl'}>
        {loading ? <Loader/> : <>

            <RadioGroup value={currency} onChange={setCurrency} p='8'>
                <HStack>
                    <Radio value='inr'>Inr</Radio>
                    <Radio value='usd'>Usd</Radio>
                    <Radio value='eur'>Eur</Radio>
                </HStack>
            </RadioGroup>
            <HStack wrap={'wrap'} justifyContent='space-evenly'>
                {
                    coins.map((i)=>(
                            <CoinCard id={i.id} key={i.id } name={i.name} img={i.image} symbol={i.symbol} currencySymbol={currencySymbol} price={i.current_price}/>
                    ))
                }
            </HStack>
            <HStack w='full' overflowX={'auto'} p='8'>
                {
                    btns.map((item,index)=>(
                        <Button key={index} bgColor={'blackAlpha.900'} color='white' onClick={()=>changePage(index+1)}>{index+1}</Button>
                    ))
                }
            </HStack>
        </>

        }
    </Container>
  )
}



export default Coins